.inc_cc_shortcode_wrap .wph-cc-shortcode {
	.wph-cc-shortcode--content {
		header {
			margin: 0 0 20px;
			h2.wph-cc-shortcode--title {
				font: 400 34px/38px $font;
				text-transform: none;
			}
			h4.wph-cc-shortcode--subtitle {
				font: 400 16px/24px $font--alt;
				text-transform: none;
			}
			h2.wph-cc-shortcode--title,
			h4.wph-cc-shortcode--subtitle {
				margin: 0 0 8px;
				&:last-child {
					margin: 0;
					padding: 0;
				}
			}
		}
		.wph-cc-shortcode--message {
			width: 100%;
			* {
				&:not(.button):not(.wph-cc-shortcode--cta) {
					color: $gray-dark;
				}
				&:last-child {
					margin-bottom: 0;
				}
			}
			h1, h2, h3, h4, h5, h6, p, a, em, ul, ol, li, strong, blockquote {
				margin: 0;
				padding: 0;
				color: $gray-dark;
				text-transform: none;
			}
			h1, h2, h3, h4, h5, h6 {
				color: $black;
			}
			h1, h2, h3 {
				margin: 0 0 0.25em;
				&:last-child {
					margin: 0;
				}
			}
			h1 {
				font: 400 42px/54px $font;
				@media (max-width: 770px) {
					font-size: 38px;
					line-height: 50px;
				}
			}
			h2 {
				font: 400 34px/38px $font;
				@media (max-width: 770px) {
					font-size: 30px;
					line-height: 34px;
				}
			}
			h3 {
				font: 400 28px/34px $font;
				@media (max-width: 770px) {
					font-size: 24px;
					line-height: 30px;
				}
			}
			h4, h5, h6 {
				margin: 0 0 0.8em;
				&:last-child {
					margin: 0;
				}
			}
			h4 {
				font: 400 16px/24px $font--alt;
			}
			h5 {
				font: 400 14px/20px $font--alt;
			}
			h6 {
				font: 400 12px/16px $font--alt;
			}
			&, p, ul, ol, li {
				font: 400 14px/20px $font--alt;
			}
			p {
				margin: 0 0 1em;
				&:last-child {
					margin: 0;
				}
			}
			a {
				color: $green;
				transition: 0.2s ease;
				&:hover {
					color: $green-dark;
				}
			}
			ul, ol {
				position: relative;
				margin: 0 0 1em;
				list-style: none;
				li {
					margin: 0 0 0.4em;
					&:last-child {
						margin: 0;
					}
				}
				&:last-child {
					margin: 0;
				}
			}
			ul li {
				position: relative;
				padding: 0 0 0 20px;
				&:before {
					content: " ";
					width: 6px;
					height: 6px;
					display: block;
					position: absolute;
					top: 6px;
					left: 0;
					border-radius: 100%;
					background: $gray-dark;
				}
			}
			ol {
				counter-reset: my-counter;
				li {
					&:before {
						content: counter(my-counter) ".";
						counter-increment: my-counter;
						margin-right: 11px;
					}
				}
			}
			figure {
				max-width: 100%;
				height: auto;
				img {
					display: block;
				}
				.wp-caption-text {
					margin: 10px 0 0;
					font: 400 12px/16px $font--alt;
				}
				&.alignnone {
					display: block;
					margin: 0;
					.wp-caption-text {
						margin: 10px 0 15px;
					}
				}
				&.alignleft {
					max-width: 450px;
					float: left;
					display: inline-block;
					margin: 0 20px 0 0;
				}
				&.aligncenter {
					display: block;
					margin: 0 auto;
					.wp-caption-text {
						margin: 10px 0 15px;
						text-align: center;
					}
				}
				&.alignright {
					max-width: 450px;
					float: right;
					display: inline-block;
					margin: 0 0 0 20px;
				}
			}
			img {
				max-width: 100%;
				height: auto;
				&.alignnone {
					display: block;
				}
				&.alignleft {
					float: left;
					display: inline-block;
					margin: 0 20px 0 0;
				}
				&.aligncenter {
					float: none;
					display: block;
					margin: 0 auto;
				}
				&.alignright {
					float: right;
					display: inline-block;
					margin: 0 0 0 20px;
				}
			}
		}
	}
	.wph-cc-shortcode--image,
	.wph-cc-shortcode--image_full {
		overflow: hidden;
		margin: 0;
		padding: 0;
		img {
			width: auto;
			max-width: 100%;
			height: auto;
			max-height: 100%;
			display: block;
			position: relative;
			margin: 0 auto;
			object-fit: cover;
			@media (max-width: 770px){
				width: 100%;
				max-width: none;
				max-height: none;
				margin: 105px auto 0;
			}
		}
		@media (max-width: 770px) {
			max-height: 250px;
		}
	}
	.wph-cc-shortcode--image_full {
		max-height: 600px;
	}
	.wph-cc-shortcode--cta,
	.wph-cc-shortcode--message a.wph-cc-shortcode--cta {
		width: auto;
		max-width: 100%;
		height: 36px;
		padding: 10px 20px;
		border-radius: 3px;
		background: $green;
		color: $white;
		font: 400 14px/36px $font--alt;
		transition: 0.2s ease;
		&:hover {
			background: $green-dark;
			color: $white;
		}
	}
	.wph-cc-shortcode--hide, footer {
		margin-top: 15px;
		text-align: center;
		a {
			color: $green;
			font: 400 12px/20px $font--alt;
			transition: 0.2s ease;
			&:hover {
				color: $green-dark;
			}
		}
	}
}