.wph-cc-shortcode.wph-cc-shortcode--cabriolet {
	header {
		h2.wph-cc-shortcode--title,
		h4.wph-cc-shortcode--subtitle {
			text-align: left;
		}
	}
	section {
		display: flex;
		flex-wrap: wrap;
		border-radius: 5px;
		background: $white;
		.wph-cc-shortcode--image {
			flex: 0 200px;
			&_full {
				flex: 1;
			}
		}
		.wph-cc-shortcode--message {
			flex: 1;
			padding: 20px;
		}
		.wph-cc-shortcode--clear {
			margin-top: 15px;
			&:not(:first-child) {
				margin: 15px 0 0;
			}
			a:not(:first-child) {
				margin-left: 20px;
			}
			a:not(.wph-cc-shortcode--cta) {
				color: $green;
				&:hover {
					color: $green-dark;
				}
			}
		}
		// Image: Align to left
		&.wph-cc-shortcode--image_left {
			flex-direction: row;
			@media (max-width: 770px){
				flex-direction: column;
			}
		}
		// Image: Align to right
		&.wph-cc-shortcode--image_right {
			flex-direction: row-reverse;
			@media (max-width: 770px){
				flex-direction: column-reverse;
			}
		}
	}
}

// Image: Border Radius
.wph-cc-shortcode.wph-cc-shortcode--cabriolet {
	.wph-cc-shortcode--content {
		// Image: Left
		.wph-cc-shortcode--image_left {
			.wph-cc-shortcode--image,
			.wph-cc-shortcode--image img {
				border-top-left-radius: inherit;
				@media (min-width: 771px) {
					border-bottom-left-radius: inherit;
				}
				@media (max-width: 770px) {
					border-top-right-radius: inherit;
				}
			}
		}
		// Image: Right
		.wph-cc-shortcode--image_right {
			.wph-cc-shortcode--image,
			.wph-cc-shortcode--image img {
				border-bottom-right-radius: inherit;
				@media (min-width: 771px) {
					border-top-right-radius: inherit;
				}
				@media (max-width: 770px) {
					border-bottom-left-radius: inherit;
				}
			}
		}
		// Image: Full
		.wph-cc-shortcode--image_left,
		.wph-cc-shortcode--image_right {
			.wph-cc-shortcode--image_full,
			.wph-cc-shortcode--image_full img {
				border-radius: inherit;
			}
		}
	}
}